<template>
  <Swiper-container class="text-center"
                    navigation="true"
                    pagination="true"
                    init="false"
                    style="max-height:200px;"
                    @slidechange="slidechange">
    <Swiper-slide v-for="num in 10" :key="`swiper-${num}`">
      <img src='https://picsum.photos/166/100' class="w-100 h-100" alt=''>
      <span class="position-absolute-center num" style="font-size:50px;">{{ num }}</span>
      <span class="position-absolute top-0 bottom-0 start-0 end-0" style="background-color:rgba(0,0,0,30%);"></span>
    </Swiper-slide>
  </Swiper-container>

  <div class="d-flex justify-content-center align-items-center mt-2">
    <button type="button" class="btn btn-light border p-1" @click="move('left')"></button>
    <input type="input" class="form-control text-center mx-1"
           v-model.number="realIndex"
           @change="slideTo"
           @keydown.up="move('left')"
           @keydown.down="move('right')"
           @keydown.right="move('right')"
           @keydown.left="move('left')"
           style="width:50px;height:34.5px;">
    <button type="button" class="btn btn-light border p-1" @click="move('right')"></button>
  </div>
</template>

<script setup>

import { ref, onMounted } from 'vue'
import 'swiper/css'

let swiperEl = null
const swiperConfig = ref(null)
const realIndex = ref(0)

onMounted(() => {
  swiperEl = document.querySelector('Swiper-container')

  // 
  swiperConfig.value = {
    slidesPerView: 1,
    breakpoints: {
      400: {
        slidesPerView: 2,
        spaceBetween: -20
      },
      450: {
        slidesPerView: 3,
        spaceBetween: 0
      },
      767: {
        slidesPerView: 4
      },
      990: {
        slidesPerView: 5
      }
    },

    loop: true, // 
    // rewind: true, // ,() "loop"

    // resizeObserver: true, //  swiper  ResizeObserver
    // keyboard: { // 
    //   enabled: true,
    //   onlyInViewport: false
    // },
    // mousewheel: { // 
    //   invert: true
    // },
    // slideToClickedSlide: true, // true
    // updateOnWindowResize: false, // true
    // grabCursor: true, // 
    // enabled: false, //  disabled
    // direction: 'vertical', //  'horizontal' | 'vertical
    // speed: 500,
    // allowTouchMove: false, // 
    // autoplay: { // 
    //   delay: 1500
    // },
    // noSwipingSelector: 'span', // 
    // preventClicks: true // true
    // preventClicksPropagation: true // true

    injectStyles: [`
        .swiper-pagination-bullet {
          width: 15px;
          height: 15px;
          text-align: center;
          line-height: 15px;
          font-size: 12px;
          color: #000;
          opacity: 1;
          background: rgba(0, 0, 0, 0.5);
        }

        .swiper-pagination-bullet-active {
          color: #fff;
          background: #fff;
        }
        .swiper-button-prev,
        .swiper-button-next {
          color: #fff;
         }
        `],
    pagination: {
      clickable: true,
      renderBullet: function (index, className) {
        console.log(className) // swiper-pagination-bullet
        return `<span class="${className}"></span>`
        // return '<span class="' + className + '">' + (index + 1) + '</span>'
      }
    },

    // 
    // effect: 'coverflow',
    // coverflowEffect: {
    //   rotate: 30,
    //   slideShadows: true
    // },

    on: {
      init () {
        console.log('init')
      },
      beforeDestroy () {
        console.log('swiper')
      }
    }
  }

  //  Swiper 
  Object.assign(swiperEl, swiperConfig.value)

  // 
  swiperEl.initialize()
  console.log(swiperEl.swiper)

  realIndex.value = swiperEl.swiper.realIndex
})
function slidechange (e) {
  console.log('', e)
  console.log(swiperEl.swiper)
  realIndex.value = swiperEl.swiper.realIndex
}
function move (direction) {
  if (direction === 'left') {
    swiperEl.swiper.slidePrev(500, false)
  } else if (direction === 'right') {
    swiperEl.swiper.slideNext(500, false)
  }
}

</script>

<style lang='scss' scope>
</style>
